.person-form {
  width: 360px;
  height: min-content;
  padding: 20px;
  border-radius: 12px;
  border: 1px solid #e5e5e5;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  background-color: #fff;
}

.person-form form {
  font-size: 20px;
}

.person-form form .form-group {
  margin-bottom: 12px;
}
